<template>
  <div class="extract">
    <van-action-sheet v-model="extractShow" overlay :title="$t('inputFP2')" @cancel="noShowSheet" >
      <div class="input">
        <!-- 密码输入框 -->
        <van-password-input
          :value="Password"

        />
<!--        <div class="Tips" @click="forgetPassword">{{$t('WJFP')}}</div>-->
        <!-- 数字键盘 -->
        <van-number-keyboard
          :show="showKeyboard"
          @input="onInput"
          @delete="onDelete"
        />
      </div>
    </van-action-sheet>
  </div>
</template>
<script>
  import { Toast } from 'vant'
  export default {
    name: "extract",
    data(){
      return{
        extractShow:'',
        // 密码
        Password: '',
        // 键盘显示
        showKeyboard: true,
        styleOff: false,
        styleClass: {color: '#fff', 'border-color': '#FF7100', 'background-color': '#FF7100'}
      }
    },
    methods:{
      // 提现
      CashWithdrawal(){
        let that = this;
        if (that.global.ws && that.global.ws.readyState === 1) {
          that.global.ws.onopen = that.setCashWithdrawal()
        }
        that.global.ws.onmessage = function(res) {
          that.getCashWithdrawal (res)
        };
      },
      setCashWithdrawal () { // websocket连接后发送数据(send发送)
        let actions;
        let that = this
        let money = this.$route.params.money
        actions = {
          action: "outmoney",
          reqID: 84,
          data: {
            amount:money,
            drawPass:that.Password,
          }
        } // 请根据实际项目需要进行修改
        that.global.ws.send(JSON.stringify(actions))
      },
      getCashWithdrawal (e) { // 数据接收
        let that = this;
        this.redata = JSON.parse(e.data)
        console.log(this.redata)
        if(this.redata.action === "outmoney"){
          if(this.redata.data.code === 0){
            Toast({
              message: '提现成功',
              forbidClick: true,
              duration:1000,
              onClose() {
                that.$router.push({
                  name:'withdrawal'
                })
              }
            })
          }else {
            Toast.clear()
            Toast({
              message: that.redata.data.msg,
              forbidClick: true,
              duration:2000,
              onClose() {
                that.$router.push({
                  name:'withdrawal'
                })
              }
            })
          }
        }
      },
      noShowSheet(){
        this.$router.push({
          name:'withdrawal',
        })
      },
      onInput(key){
        let that = this
        this.Password = (this.Password + key).slice(0, 6)
        if (this.Password.length === 6) {
          this.styleOff = true
          Toast.loading({
            message: that.$t('Processing'),
            forbidClick: true,
            duration:3000,
            onOpened(){
              that.CashWithdrawal()
            }
          })
        }
        if (this.Password.length < 6) {
          this.styleOff = false
        }
      },
      onDelete(){
        this.Password = this.Password.slice(0, this.Password.length - 1)
        if (this.Password.length < 6) {
          this.styleOff = false
        }
      },
      forgetPassword(){
        this.$router.push({
          name:'forgetPassword'
        })
      }
    }


  }
</script>

<style scoped lang="scss">
  @import "../../../style/my";
</style>



